.spinner {
    height: 100px;
    width: 100px;
    position: relative;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}
.inner {
    height: 100px;
    width: 100px;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 100%;
    border: 4px solid transparent;
    opacity: 0.5;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.inner:nth-child(1) {
    border-top-color: #FC9504;
    border-bottom-color: #FC9504;
    -webkit-animation: rotate 2s ease infinite;
            animation: rotate 2s ease infinite;
}
.inner:nth-child(2) {
    border-left-color: #5634B2;
    border-right-color: #5634B2;
    -webkit-animation: rotate 3s ease infinite;
            animation: rotate 3s ease infinite;
}
.inner:nth-child(3) {
    border-top-color: #EB0028;
    border-bottom-color: #EB0028;
    -webkit-animation: rotate 4s ease infinite;
            animation: rotate 4s ease infinite;
}
@-webkit-keyframes rotate {
    from{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
    to{-webkit-transform: rotate(360deg);transform: rotate(360deg)}
}
@keyframes rotate {
    from{-webkit-transform: rotate(0deg);transform: rotate(0deg)}
    to{-webkit-transform: rotate(360deg);transform: rotate(360deg)}
}


